@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400&display=swap");
@mixin mobile {
  @media screen and (max-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (max-width: 768px) {
    @content;
  }
}
@mixin laptops {
  @media screen and (max-width: 1200px) {
    @content;
  }
}

:root {
  --color-primary: #650b0b;
  --color-secondary: #ff7782;
  --color-white: #202528;

  --color-background: #f6f6f9;
}

.dark-theme-variables {
  --color-background: #181a1e;
  --color-white: #202528;
  --color-dark: #edeffd;
  --color-dark-variant: #a3bdcc;
  --color-light: rgba(0, 0, 0, 0.4);
  --box-shadow: 0 2rem 3rem var(--color-light);
}

$darkColor: #a7bcff;
body {
  margin: 0;
  font-family: "Montserrat";
}
// Style de Login and Register
.formContainer {
  background-color: #272727;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: none;

  .formWrapper {
    position: absolute;
    padding: 20px 60px;
    background: rgba(0, 0, 0, 0.85);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    box-shadow: 0px 0px 62px rgba(78, 42, 157, 0.8);

    img {
      width: 190px;
    }
    .logo {
      color: rgb(218, 218, 218);
      font-weight: bold;
      font-size: 24px;
    }

    .title {
      color: #ffffff;

      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 15px;

      input {
        border: none;

        background: radial-gradient(
          90.16% 143.01% at 15.32% 21.04%,
          rgba(255, 255, 255, 0.2) 0%,
          rgba(255, 255, 255, 0.0447917) 77.08%,
          rgba(255, 255, 255, 0) 100%
        );
        background-blend-mode: overlay, normal;
        backdrop-filter: blur(10.07811px);
        padding: 15px;
        width: 250px;
        // border-bottom: 1px solid white;
        border-radius: 12px;
        font-family: "Montserrat";

        &::placeholder {
          color: rgb(175, 175, 175);
          border: none;
        }

        &:focus {
          color: rgb(175, 175, 175);
          border: none;
        }
      }

      button {
        margin-top: 40px;
        background: linear-gradient(90.3deg, #9c3fe4 0.16%, #c65647 101.62%);
        border-radius: 12px;
        color: white;
        padding: 15px;
        font-weight: bold;
        border: none;
        cursor: pointer;
        font-family: "Montserrat";
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        color: white;
        font-size: 12px;
        cursor: pointer;
      }

      img {
        width: 32px;
        color: white;
      }
    }

    p {
      color: white;
      font-size: 12px;
      margin-top: 10px;
    }
  }
}

// Style of Home
.home {
  background-color: #2f2f2f;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .container {
    border: 1px solid white;
    border-radius: 10px;
    width: 65%;
    height: 80%;
    // width: max-content;
    // height: max-content;
    display: flex;
    overflow: hidden;
    @include tablet {
      width: 90%;
    }

    //Sidebar Style
    .sidebar {
      flex: 1;
      background-color: rgb(96, 175, 175);
      position: relative;
      //Navbar Style
      .navbar {
        display: flex;
        align-items: center;
        background-color: brown;
        height: 50px;
        padding: 10px;
        justify-content: space-between;
        color: beige;

        .logo {
          font-weight: bold;
          @include tablet {
            display: none;
          }
        }

        .user {
          display: flex;
          gap: 10px;

          img {
            background-color: white;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            object-fit: cover;
          }

          //Logout Button
          button {
            background-color: #a7bcff;
            color: white;
            border: none;
            border-radius: 3px;
            cursor: pointer;
            @include tablet {
              position: absolute;
              bottom: 10px;
            }
          }
        }
      }

      //Search Style
      .search {
        border-bottom: 1px solid gray;

        .searchForm {
          padding: 10px;

          input {
            background-color: transparent;
            border: none;
            color: white;
            outline: none;

            &::placeholder {
              color: lightgray;
            }
          }
        }
      }

      //User Chat Style
      .userChat {
        padding: 10px;
        display: flex;
        align-items: center;
        gap: 10px;
        color: white;
        cursor: pointer;

        &:hover {
          background-color: green;
        }

        //User Chat Profile Photo Style
        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          object-fit: cover;
        }

        .userChatInfo {
          span {
            font-size: 18px;
            font-weight: 500;
          }

          p {
            font-size: 14px;
            color: lightblue;
          }
        }
      }
    }

    // Individual Chat Stlye
    .chat {
      flex: 2;
      background-color: #727272;
      padding: 0 0 12px 0;

      .chatInfo {
        height: 50px;
        background-color: cadetblue;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        color: #a7bcff;
      }

      .chatIcons {
        display: flex;
        gap: 10px;

        img {
          height: 24px;
          cursor: pointer;
        }
      }

      .messages {
        background-color: #a7bcff;
        padding: 10px;
        height: calc(100% - 160px);
        overflow: scroll;
        // padding: 10px 20px 30px 40px;
        .message {
          display: flex;
          gap: 20px;
          margin-bottom: 20px;

          .messageInfo {
            display: flex;
            flex-direction: column;
            color: gray;
            font-weight: 300;

            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              object-fit: cover;
            }
          }
          .messageContent {
            max-width: 80%;
            display: flex;
            flex-direction: column;
            gap: 10px;

            p {
              background-color: white;
              padding: 10px 20px;
              border-radius: 0px 10px 10px 10px;
              max-width: max-content;
            }

            img {
              width: 50%;
            }
          }

          &.owner {
            flex-direction: row-reverse;
            .messageContent {
              align-items: flex-end;
              p {
                background-color: #182241;
                color: white;
                border-radius: 10px 0px 10px 10px;
              }
            }
          }
        }
      }

      .input {
        height: 50px;
        background-color: white;
        padding: 10px;
        display: flex;
        align-items: center;
        align-content: space-between;
        border-radius: 15px;
        // margin-top: 20px;
        // margin-bottom: 10px;
        input {
          width: 100%;
          border: none;
          outline: none;
          color: rgb(122, 82, 82);
          font-size: 18px;

          &::placeholder {
            color: lightcoral;
          }
        }

        .send {
          display: flex;
          align-items: center;
          gap: 10px;

          img {
            height: 24px;
            cursor: pointer;
          }

          button {
            border: none;
            padding: 10px 15px;
            color: white;
            background-color: violet;
            cursor: pointer;
          }
        }
      }
    }
  }
}
